.stars {
  display: flex;
}

.stars__star {
  width: 16px;
  height: 16px;

  margin-right: 4px;
  background-repeat: no-repeat;
  background-position: center;

  background-image: url(../../images/star.svg);
}

.stars__star:last-child {
  margin-right: 0;
}

